import { PropsWithChildren, ReactNode, useRef, useState } from 'react';

import Typography from 'components/ui/Typography';

import classNames from 'classnames';

interface TooltipProps extends PropsWithChildren {
  id: string;
  content?: ReactNode;
  hasArrow?: boolean;
}

interface TooltipBodyProps extends PropsWithChildren {
  variant?: 'body' | 'caption';
  size?: 'xs' | 'sm' | 'base' | 'lg';
  weight?: 400 | 500;
  color?: string;
}

const TooltipTitle = ({ children }: PropsWithChildren) => {
  return (
    <Typography variant="caption" weight={500} color="text-gray-800">
      {children}
    </Typography>
  );
};

const TooltipBody = ({
  variant = 'caption',
  size = 'base',
  weight = 400,
  color = 'text-gray-700',
  children
}: TooltipBodyProps) => {
  return (
    <Typography variant={variant} size={size} weight={weight} isParagraph color={color}>
      {children}
    </Typography>
  );
};

const Tooltip = ({ id, content, hasArrow = true, children }: TooltipProps) => {
  const [showTooltip, setShowTooltip] = useState(false);
  const tooltipRef = useRef<HTMLDivElement | null>(null);

  const handleMouseEnter = () => {
    setShowTooltip(true);
  };

  const handleMouseLeave = () => {
    setShowTooltip(false);
  };

  return (
    <div
      className="flex relative cursor-help"
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      {children}
      {showTooltip && (
        <>
          <div
            ref={tooltipRef}
            id={id}
            className={classNames(
              'flex flex-col gap-y-1 absolute -bottom-[0.875rem] left-1/2 z-[99] min-w-[20rem] w-fit py-2 px-3 bg-white rounded-card-md shadow-tooltip transform -translate-x-1/2 translate-y-full'
            )}
          >
            {content}
            {hasArrow && <div className="qb-tooltip-arrow" />}
          </div>
        </>
      )}
    </div>
  );
};

Tooltip.Title = TooltipTitle;
Tooltip.Body = TooltipBody;

export default Tooltip;
